.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.custom-me{
  margin-right: 200px;
}
.nav-link.active:hover {
  border: 1px solid none;
  border-radius: 5%;
  background-color: blue;
  padding: 0.5rem 0.5rem;
  
}
.custom-mb:hover{
  border: 1px solid white;
  border-radius: 20%;
  background-color: blueviolet;
}

.animated-text {
  opacity: 0;
  transform: translateY(10px);
  animation: slideInUp 4s ease-in-out forwards;
  
}

.animated-button {
  opacity: 0;
  transform: translateX(-20px);
  animation: fadeInRight 4s ease-in-out forwards 0.5s;
}
.form-label{
  color: white;
}
@keyframes slideInUp {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes fadeInRight {
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

/* border:'1px solid white' */
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.custom-spinner {
  width: 50px; /* Adjust the width as needed */
  height: 50px; /* Adjust the height as needed */
}

.spinner-inner {
  width: 50px; /* Adjust the width as needed */
  height: 50px; /* Adjust the height as needed */
}






.spinner-circle {
  border-width: 3px; /* Adjust the border width as needed */
  border-top-color: #007bff; /* Adjust the spinner color as needed */
  position:relative;
  width: 120%;
  height: 120%;
  border: 5px solid transparent;
  border-top-color: white;
  border-right-color: blue; /* Change the color of the right border */
  border-bottom-color: red; /* Change the color of the bottom border */
  border-left-color: yellow; /* Change the color of the left border */
  border-radius: 50%;
  animation: spin 3s linear infinite;
}

