body {
  overflow: hidden;
  overflow-y: scroll; /* Hide scrollbars */
}


/* Adjust this value as needed */
/* Add a vertical scrollbar if content exceeds the max height */



@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}